.container {

    grid-template-rows: calc(100vh / 6) calc(100vh / 6) calc(100vh / 6) calc(100vh / 6) calc(100vh / 6) calc(100vh / 6);

}

a {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-size: 6vw;
    text-decoration: none;
    color: rgb(211, 211, 211);
    text-shadow: 3px 3px #2b2b2b;
}

span {
    display: contents;
}

@media (max-width: 1024px) {
    .container {
        /*grid-template-rows: calc(100vh / 7) calc(100vh / 7) calc(100vh / 7) calc(100vh / 7) calc(100vh / 7) calc(100vh / 7);*/
        display: block;
    }

    a {
        font-size: 11vw;
        transform: translateX(0)!important;
    }

    img {
        position: absolute;
    }
}

@media (min-width: 1024px) {
    /*
    a:hover {
        background-color: rgba(0, 0, 0, 0.5);
    }*/
    
    a:hover img {
        opacity: 1 !important;
    }
}

