@font-face {
    font-family: 'Ampersand';
    src: url(static/font/ampersand.woff2) format("woff2");
    font-weight: 400;
    font-display: swap;
    font-style: normal
}

html, body {margin: 0 auto; height: 100%; overflow: hidden; text-align: center;}

body {
    background-color: white;
    font-family: 'Ampersand', 'normal';
    background-color: black;
}

#loader-wrapper {
    z-index: 2;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0; 
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}